<template>
	<div>
		<div class="providerAdd">
            <form action="#">
                <!--div的class 为error是验证错误，ok是验证成功-->
                <div>
                    <label for="userName">用户名称：</label>
                    <input type="text" name="userName" id="userName" ref="userName" :placeholder="singleUserInfor.userName"/>
                    <span >*</span>
                </div>

                <div>
                    <label >用户性别：</label>

                    <select name=""  ref="sex">
                        <option>男</option>
                        <option>女</option>
                        <p>{{sex}}</p>
                    </select>
                </div>
                <div>
                    <label for="data">出生日期：</label>
                    <input type="text" name="data" id="data" ref="birth" :placeholder="singleUserInfor.birth"/>
                    <span >*</span>
                </div>
                <div>
                    <label for="userphone">用户电话：</label>
                    <input type="text" name="userphone" id="userphone" ref="userphone" :placeholder="singleUserInfor.userphone"/>
                    <span >*</span>
                </div>
                <div>
                    <label for="userAddress">用户地址：</label>
                    <input type="text" name="userAddress" id="userAddress" ref="userAddress" :placeholder="singleUserInfor.userAddress"/>
                </div>
                <div>
                    <label >用户类别：</label>
                    <input type="radio" name="userlei" v-model="userlei" value="管理员"/>管理员
                    <input type="radio" name="userlei" v-model="userlei" checked value="经理"/>经理
                    <input type="radio" name="userlei" v-model="userlei" value="普通用户"/>普通用户
                </div>
                <div class="providerAddBtn">
                    <!--<a href="#">保存</a>-->
                    <!--<a href="userList.html">返回</a>-->
                    <input type="button" value="保存" @click="userupdate"/>
                    <input type="button" value="返回" @click="userUpdateBack(false)"/>
                </div>
            </form>
        </div>
	</div>
</template>

<script>
	import store from '@/vuex/store.js'
	import {mapState,mapMutations} from 'vuex'
	export default{
		store,
		data(){
			return{
				sex:'',
				userlei:''
			}
		},
		computed:{
			...mapState(['singleUserInfor','showUserInfor'])
		},
		methods:{
			...mapMutations(['userUpdateBack']),
			userupdate(){
				var userName=this.$refs.userName.value;
				var sex=this.$refs.sex.value;
				var birth=this.$refs.birth.value;
				var userphone=this.$refs.userphone.value;
				var userAddress=this.$refs.userAddress.value;
				var userlei=this.userlei;
				var _this=this
				this.$axios({
					url:'/VueHandler/user?action=update',
					method:'post',
					data:{
						userId:_this.singleUserInfor.userId,
						userName:userName,
						sex:sex,
						birth:birth,
						userphone:userphone,
						userAddress:userAddress,
						userlei:userlei
					}
				}).then(function(response){
					console.log(response)
					if(response.data.success){
						_this.userUpdateBack(false);
						_this.showUserInfor()
					}else{
						alert(response.data.err)
					}
					
				}) 
			}
		},
		mounted(){
			this.isPay=this.singleUserInfor.isPay
		}
	}
</script>

<style scoped="scoped">
	.providerAdd {
	    border: 1px solid #9ac70f;
	    padding: 20px;
	    margin: 20px;
	}
	
	.providerAdd form {
	
	}
	
	.providerAdd div {
	    margin-top: 12px;
	    margin-bottom: 12px;
	}
	
	.providerAdd label {
	    width: 200px;
	    display: inline-block;
	    text-align: right;
	}
	
	.providerAdd input ,.providerAdd select{
	    width: 260px;
	    height: 30px;
	    line-height: 30px;
	    border-radius: 4px;
	    outline: none;
	    padding-left: 10px;
	    border: 1px solid #4987c0;
	    box-shadow: 1px 1px 1px #99afc4;
	}
	
	.providerAdd input:focus,.providerAdd select:focus {
	    border: 1px solid #0e56a8;
	    background: rgba(238, 236, 240, 0.2);
	    -webkit-box-shadow: 0px 1px 4px 0px rgba(168, 168, 168, 0.9) inset;
	    -moz-box-shadow: 0px 1px 4px 0px rgba(168, 168, 168, 0.9) inset;
	    box-shadow: 0px 1px 4px 0px rgba(168, 168, 168, 0.9) inset;
	}
	
	.providerAdd span {
	    margin-left: 20px;
	    color: #faca0d;
	}
	
	/*验证错误*/
	.providerAdd .error span {
	    color: red;
	}
	
	.providerAdd .error input {
	    border: 1px solid red;
	}
	
	/*验证成功*/
	.providerAdd .ok span {
	    color: green;
	}
	
	.providerAdd .ok input {
	    border: 1px solid green;
	}
	
	.providerAddBtn {
	    margin-left: 240px;
	    padding-top: 20px;
	}
	
	.providerAddBtn a ,
	.providerAddBtn input[type='submit'],
	.providerAddBtn input[type='button'] {
	    display: inline-block;
	    width: 100px;
	    height: 30px;
	    line-height: 30px;
	    text-align: center;
	    border-radius: 4px;
	    border: transparent;
	    background: linear-gradient(to bottom, #85c0ec, #6aa7d6, #508dc6, #306fb4, #17559e);
	    color: #fff;
	    cursor: pointer;
	    font-weight: bold;
	    font-size: 14px;
	    vertical-align: top;
	}
	
	.providerAddBtn a:active,
	.providerAddBtn a:focus,
	.providerAddBtn a:hover,
	.providerAddBtn input[type='submit']:hover,
	.providerAddBtn input[type='submit']:focus{
	    background: linear-gradient(to bottom, #7aaed4, #578bb4, #406e99, #225792, #0d2d54);
	}
	
	.providerAdd input[type='radio']{
	    width: 20px;
	    height: 14px;
	    line-height:12px;
	    border-radius: 4px;
	    outline: none;
	    padding-left: 10px;
	    border: none;
	    box-shadow: none;
	}
</style>


